body {
  background-color: #000;
  background-image: url("background.05736e9e.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 30px;
  font-family: Mulish;
}

.header {
  display: flex;
}

.header-logo {
  margin-right: 68px;
}

.header-container {
  flex-direction: column;
  display: flex;
}

.header-title {
  color: #ff1919;
  text-align: center;
  text-shadow: 2px 1.5px #c1deebcc;
  -webkit-text-stroke-width: 1.5px;
  -webkit-text-stroke-color: #065a76;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-size: 75px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.header-mind-hero {
  text-align: center;
  -webkit-text-stroke-width: 1.5px;
  -webkit-text-stroke-color: #065a76;
  text-transform: uppercase;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(#cd370e .01%, #000 79.07%) text;
  font-size: 75px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.header-description {
  color: #fff;
  text-shadow: 2px 1.5px #c1deebcc;
  -webkit-text-stroke-width: 1.5px;
  -webkit-text-stroke-color: #065a76;
  letter-spacing: 1.05px;
  text-transform: uppercase;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  font-family: Mulish;
  font-size: 35px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.main-pages {
  flex-direction: column;
  display: flex;
}

.marquee {
  width: 100%;
  padding: 25px 0;
  position: relative;
  overflow: hidden;
}

.marquee span {
  white-space: nowrap;
  color: #ff1919;
  text-align: center;
  text-shadow: 2px 1.5px #c1deebcc;
  -webkit-text-stroke-width: 1.5px;
  -webkit-text-stroke-color: #065a76;
  text-align: center;
  letter-spacing: 1.2px;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  animation: 10s linear infinite scrollText;
  position: absolute;
  top: 0;
}

@keyframes scrollText {
  0% {
    left: 100%;
  }

  100% {
    left: -100%;
  }
}

.marquee span:nth-child(2) {
  animation-delay: -5s;
}

.main-page-box {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.main-page-list {
  flex-wrap: wrap;
  gap: 28px;
  display: flex;
}

.main-page-item {
  height: 172px;
}

.main-page-img {
  width: 100%;
  height: 135px;
}

.main-page-item:first-child, .main-page-item:nth-child(2) {
  margin-right: 97px;
}

.main-page-item:nth-child(4), .main-page-item:nth-child(5) {
  margin-right: 45px;
}

.main-page-item:nth-child(4) .main-page-img {
  border-radius: 20px;
}

.main-page-text {
  color: #2020c5;
  text-shadow: 2px 1.5px #c1deebcc;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #065a76;
  text-align: center;
  letter-spacing: .75px;
  margin-top: 7px;
  margin-left: auto;
  margin-right: auto;
  font-size: 25px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.main-page-last-img {
  width: 216px;
  height: 287px;
  margin-bottom: 24px;
}

.main-page-drone {
  width: 373px;
}

.footer {
  flex-direction: column;
